<template>
  <div class="copy">
    <span
      v-clipboard:copy="copyKey"
      v-clipboard:success="onCopy"
      v-clipboard:error="onError"
    >
      </span
    >
  </div>
</template>
<script>
export default {
  data() {
    return {}
  },
  props: {
    copyKey: {
      type: String,
      default: ''
    }
  },
  methods: {
    // 
    onCopy(e) {
      this.$message.success('')
    },
    // 
    onError(e) {
      this.$message.success('')
    }
  }
}
</script>
<style lang="scss">
.copy {
  display: inline-block;
  margin-left: 4px;
  color: #409eff;
  cursor: pointer;
}
</style>
