/**
 *pxrem
 * @export
 * @param {*} px
 * @returns
 */
export function px2rem(px) {
  return px / 100 + 'rem'
}
/**
 *rempx
 * @export
 * @param {*} rem
 * @returns
 */
export function rem2px(rem) {
  var w = document.documentElement.clientWidth
  var b = 1920
  var f = 100
  rem = parseFloat(rem)
  return ((rem * w) / b) * f
}
