export const brightenKeyword = (val, keyword) => {
  val = val + ''
  const replaceReg = new RegExp(keyword, 'g')
  if (keyword) {
    // v-html
    const replaceString = '<font color="#F0717B">' + keyword + '</font>'
    // 
    const newVal = val.replace(replaceReg, replaceString)
    return newVal
  } else {
    return val
  }
}
// urla
export const replaceURLWithHTMLLinks = text => {
  // eslint-disable-next-line no-useless-escape
  var exp = /(\b(https?|ftp|file):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/i
  return text.replace(exp, "<a target='_blank' href='$1'>$1</a>")
}
// urla
export const msgIsLink = text => {
  // eslint-disable-next-line no-useless-escape
  var exp = /(\b(https?|ftp|file):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/i
  return exp.test(text)
}
export function countTime(startTime, myendDate) {
  // 
  // 
  var date = new Date()
  var now = date.getTime()
  // 
  var endDate = new Date(myendDate)
  var end = endDate.getTime()
  // 
  var leftTime = end - now
  let d, h, m, s
  //  d,h,m,s
  if (leftTime >= 0) {
    d = Math.floor(leftTime / 1000 / 60 / 60 / 24)
    h = Math.floor((leftTime / 1000 / 60 / 60) % 24)
    m = Math.floor((leftTime / 1000 / 60) % 60)
    s = Math.floor((leftTime / 1000) % 60)
  }
  // console.log(this.s);
  const timeObj = {
    dayDiff: d || 0,
    hours: !h
      ? '00'
      : h.toString().length === 1
      ? '0' + h.toString()
      : h.toString(),
    minutes: !m
      ? '00'
      : m.toString().length === 1
      ? '0' + m.toString()
      : m.toString(),
    seconds: !s
      ? '00'
      : s.toString().length === 1
      ? '0' + s.toString()
      : s.toString()
  }
  return timeObj
  // countTime
  // setTimeout(countTime, 1000)
}
// 
export const NumRound2 = num => {
  let newNum = Math.round((num * 1000) / 10) / 100
  const numToString = newNum.toString().split('.')
  if (numToString.length === 1) {
    newNum = newNum.toString() + '.00'
    return newNum
  }
  if (numToString.length > 1) {
    if (numToString[1].length < 2) {
      newNum = newNum.toString() + '0'
    }
    return newNum
  }
}
// jsonget
export function jsonToParams(json) {
  var params = Object.keys(json)
    .map(function(key) {
      return encodeURIComponent(key) + '=' + encodeURIComponent(json[key])
    })
    .join('&')
  return params
}
// 
export const getAge = (birthDay, curTime = new Date()) => {
  birthDay = new Date(birthDay)
  if (
    new Date(birthDay).toDateString() === 'Invalid Date' ||
    curTime.toDateString() === 'Invalid Date'
  ) {
    return { year: 0, month: 0, date: 0, hour: 0, minutes: 0 }
  }
  if (birthDay > curTime) {
    return { year: 0, month: 0, date: 0, hour: 0, minutes: 0 }
  }
  const birthYear = birthDay.getFullYear()
  const birthMonth = birthDay.getMonth() + 1
  const birthDate = birthDay.getDate()
  const birthHour = birthDay.getHours()
  const birthMin = birthDay.getMinutes()
  const curYear = curTime.getFullYear()
  const curMonth = curTime.getMonth() + 1
  const curDate = curTime.getDate()
  const curHour = curTime.getHours()
  const curMin = curTime.getMinutes()
  let minutes = curMin - birthMin
  let hour = curHour - birthHour
  if (minutes < 0) {
    hour = hour - 1
    minutes = 60 + minutes
  }
  let date = curDate - birthDate
  if (hour < 0) {
    date = date - 1
    hour = 24 + hour
  }
  let month = curMonth - birthMonth
  if (date < 0) {
    month = month - 1
    const days = new Date(curTime.getFullYear(), month, 0).getDate()
    date = days + date
  }
  let year = curYear - birthYear
  if (month < 0) {
    year = year - 1
    month = 12 + month
  }
  return { year, month, date, hour, minutes }
}
// 
export function secondToHMS(second) {
  var min = Math.floor(second % 3600)
  const hms =
    addZero(Math.floor(second / 3600)) +
    ':' +
    addZero(Math.floor(min / 60)) +
    ':' +
    addZero(second % 60)
  return hms
}

export function formatSeconds(value) {
  let result = parseInt(value)
  let h =
    Math.floor(result / 3600) < 10
      ? '0' + Math.floor(result / 3600)
      : Math.floor(result / 3600)
  let m =
    Math.floor((result / 60) % 60) < 10
      ? '0' + Math.floor((result / 60) % 60)
      : Math.floor((result / 60) % 60)
  let s =
    Math.floor(result % 60) < 10
      ? '0' + Math.floor(result % 60)
      : Math.floor(result % 60)
  result = `${h}:${m}:${s}`
  return result
}
