// import _ from 'lodash'
export function getArrOrLabel(arr, value, type) {
  if (type) {
    return arr
  } else {
    const findObj = arr.find(arr => arr.value === value)
    if (findObj) {
      return findObj.label
    }
    return ''
  }
}
/**
 *
 * @param {*} value value,label
 * @param {*} showNfm 
 * @param {*} type arr,getLabelByValue
 * valuearrgetLabelByValue
 */
export function sectionOffice(value, type, showNfm) {
  let arr = [
    {
      label: '',
      value: 'pediatric'
    },
    {
      label: '',
      value: 'obstetrics'
    }
  ]
  if (showNfm) {
    arr.push({
      label: '',
      value: 'endocrine'
    })
  }
  return getArrOrLabel(arr, value, type)
}
