export function getDepartment(value, type) {
  let arr = [
    {
      label: '',
      value: 'ERKE'
    },
    {
      label: '',
      value: 'CHANKE'
    }
  ]
  return getArrOrLabel(arr, value, type)
}
export function systemRole(value, type, department) {
  const arr = [
    {
      value: 'hm',
      label: department === 'chanke' ? '' : ''
    },
    {
      value: 'doctor',
      label: ''
    },
    {
      value: 'dietician',
      label: ''
    }
  ]
  return getArrOrLabel(arr, value, type)
}

