import {
  getCustomerTree,
  getTimeLineInfo,
  getMockChatByBabySession
} from '@/api/chat'
import $store from '../../store'
import { getLastAsk } from '@/api/bout'
import { getCurrentCustomer } from '@/api/userInfo'
import { Message } from 'element-ui'
// 
import {
  getLastC2CHistoryMessages,
  messageLocation,
  fdAskLocation
} from '@/api/message'
import _ from 'lodash'
const state = {
  chatListType: 'messageQueue', //
  ended_type: '', //
  show_suggestion_send: '', //
  // 
  cusSearchValue: '',
  // 
  currentSession: {},
  consultBabyId: '',
  ask_mode: 'IM', //ROUND  IM
  sessionLastAsk: {}, //
  activedBabyId: '', //id
  activedUserInfo: {}, //
  currentCustomer: '', //
  activedUserId: '', //id
  // 
  currentBabyList: [],
  activedBaby: {}, // 
  customerTree: [], // 
  serviceItem: [],
  unReadCount: 0,
  isMyUser: true, // 
  lastAsk: {},
  // 
  timeLineInfo: [],
  currentTextAnswer: {}, // 
  currentTransferTextAnswer: {}, // 
  currentVoiceAnswer: {}, // 
  manageAnswer: {},
  activedBabyIndex: 0 // index
}
const mutations = {
  SET_CUS_SEARCHVALUE: (state, searchValue) => {
    state.cusSearchValue = searchValue
  },
  SET_CURRENT_SESSION: (state, obj) => {
    state.currentSession = obj
    state.sessionLastAsk = obj.last_ask ? obj.last_ask : ''
    state.activedBabyId = obj && obj.family_member ? obj.family_member._id : ''
    state.activedUserInfo = obj && obj.user ? obj.user : ''
    state.activedUserId = obj && obj.user ? obj.user._id : ''
    state.ask_mode = obj.ask_mode ? obj.ask_mode : 'ROUND'
  },
  SET_LAST_ASK: (state, last_ask) => {
    state.lastAsk = last_ask
  },
  SET_SERVICE_ITEMS: (state, item) => {
    state.serviceItem = item
  },
  UPDATE_CURRENT_SESSION: (state, info) => {
    state.currentSession = Object.assign(state.currentSession, info)
  },
  SET_ACTIVED_USERID: (state, id) => {
    state.activedUserId = id
  },
  SET_ACTIVED_BABY: (state, info) => {
    state.activedBaby = info
    // ,
    if (info.is_asking) {
      state.consultBabyId =
        info && info.family_member ? info.family_member._id : ''
    }
  },
  SET_ACTIVED_BABY_INDEX: (state, info) => {
    state.activedBabyIndex = info
  },
  SET_ACTIVED_BABYID: (state, id) => {
    state.activedBabyId = id
  },
  SET_CUSTOMERTREE: (state, customerTree) => {
    state.customerTree = customerTree
  },
  SET_TIMELINEINFO: (state, timeLineInfo) => {
    state.timeLineInfo = timeLineInfo
  },
  SET_CURRENTCUSTOMER: (state, currentCustomer) => {
    state.currentCustomer = currentCustomer
  },
  SET_CURRENTBABYLIST: (state, currentBabyList) => {
    state.currentBabyList = currentBabyList
  },
  SET_ENDED_TYPE: (state, type) => {
    state.ended_type = type
  },
  SHOW_SUGGESTION_SEND: (state, showFlag) => {
    state.show_suggestion_send = showFlag
  },
  SET_CHAT_LIST_TYPE: (state, chatListType) => {
    state.chatListType = chatListType
  },
  IS_MY_USER: (state, flag) => {
    state.isMyUser = flag
  },
  SET_UNREAD_COUNT: (state, COUNT) => {
    state.unReadCount = COUNT
  }
}
/*
  1activedUserId
  2sessionuser
  3session
  4card,sessionsession,session
  5idsession,session
*/
const actions = {
  setActivedBabyId: ({ commit }, id) => {
    commit('SET_ACTIVED_BABYID', id)
  },
  getActivedUserInfo: ({ commit, dispatch }, params) => {
    const userId = params && params.id ? params.id : state.activedUserId
    if (userId !== state.activedUserId) {
      commit('SET_CURRENTCUSTOMER', '')
      commit('SET_CURRENTBABYLIST', [])
    }
    if (userId) {
      // 
      getCurrentCustomer(userId).then(res => {
        commit('SET_CURRENTCUSTOMER', res.data)
        commit('SET_CURRENTBABYLIST', res.data.family_member_list)
        // 
        const fmId = res.data.family_member_list.length
          ? res.data.family_member_list[0]
          : ''
        const babyId =
          params && params.babyId ? params.babyId : state.activedBabyId
        dispatch('setActivedBabyById', {
          babyId: babyId || fmId.family_member._id
        })
      })
    }
  },
  // id
  setActivedBabyById: ({ commit, dispatch }, params = {}) => {
    const babyId = params.babyId ? params.babyId : state.activedBabyId
    if (babyId) {
      const existBabyIndex = _.findIndex(state.currentBabyList, function(o) {
        return o.family_member._id === babyId
      })
      if (existBabyIndex >= 0) {
        commit('SET_ACTIVED_BABYID', babyId)
        commit('SET_ACTIVED_BABY_INDEX', existBabyIndex)
        commit('SET_ACTIVED_BABY', state.currentBabyList[existBabyIndex])
      } else {
        Message.info('')
      }
    }
    dispatch('setTrigger', 'm', {
      root: true
    })
  },
  // mocklast_ask
  async getChat({ commit, dispatch }, sessionId) {
    await getMockChatByBabySession(sessionId).then(res => {
      const resData = res.data
      commit('IS_MY_USER', false)
      if (resData) {
        dispatch('chat/setCurrentSession', resData, {
          root: true
        })
        commit('SET_LAST_ASK', resData.last_ask)
        commit('SET_SERVICE_ITEMS', resData.service_items)
        commit('UPDATE_CURRENT_SESSION', resData)
      } else {
        Message.info('session')
      }
    })
  },
  // idlast_ask
  async getLastAsk({ commit }, sessionId) {
    const sId = sessionId || $store.state.msgQueue.sessionId
    await getLastAsk(sId).then(res => {
      const list = res.data
      if (list) {
        commit('SET_LAST_ASK', list.last_ask)
        commit('SET_UNREAD_COUNT', list.un_read_count)
        commit('SET_SERVICE_ITEMS', list.service_items)
        commit('UPDATE_CURRENT_SESSION', list)
      } else {
        Message.info('last_ask')
      }
    })
  },
  setCurrentSession({ commit }, obj) {
    commit('SET_CURRENT_SESSION', obj)
  },
  async messageLocation({ dispatch }, id) {
    await messageLocation(id).then(res => {
      if (res.data.list && res.data.list.length > 0) {
        dispatch('setCurrentIMInfoMessages', [], {
          root: true
        })
        dispatch('setCurrentIMInfoMessages', res.data.list, {
          root: true
        })
        dispatch('setMessageFlag', true, {
          root: true
        })
      } else {
        dispatch('setMessageFlag', false, {
          root: true
        })
      }
    })
  },
  // id
  fdAskLocation({ commit, dispatch }, id) {
    fdAskLocation(id).then(res => {
      if (res.data.list && res.data.list.length > 0) {
        dispatch('setCurrentIMInfoMessages', res.data.list, {
          root: true
        })
        dispatch('setMessageFlag', true, {
          root: true
        })
      } else {
        Message.info('id')
        dispatch('setMessageFlag', false, {
          root: true
        })
      }
    })
  },
  getLastC2CHistoryMessages: ({ commit, dispatch }, val) => {
    //  
    const params = {
      family_member: state.currentSession.family_member._id,
      message_id: val ? val.message_id : '',
      direction: val ? val.direction : ''
    }
    getLastC2CHistoryMessages(state.currentSession.session_id, params)
      .then(res => {
        if (!val || !val.direction) {
          dispatch('setTrigger', 'm', {
            root: true
          })
          dispatch('setCurrentIMInfoMessages', res.data.message_list, {
            root: true
          })
          const haveMsg =
            res.data.message_list && res.data.message_list.length > 0
          dispatch('setMessageFlag', haveMsg, {
            root: true
          })
        } else {
          dispatch('setTrigger', 'scroll', {
            root: true
          })
          if (res.data.message_list && res.data.message_list.length > 0) {
            if (params.direction === 'up') {
              dispatch('unshiftMessages', res.data.message_list, {
                root: true
              })
            } else {
              dispatch('addMessages', res.data.message_list, {
                root: true
              })
            }
            dispatch('setMessageFlag', true, {
              root: true
            })
          } else if (
            res.data.message_list.length === 0 &&
            params.direction === 'up'
          ) {
            dispatch('setMessageFlag', false, {
              root: true
            })
          }
        }
      })
      .catch(error => {
        console.log(error)
      })
  },
  // 
  async getCustomerTree({ commit, dispatch }, type) {
    await dispatch('getMsgQueue', type, {
      root: true
    })
    if (state.chatListType !== 'messageQueue') {
      // 
      getCustomerTree().then(res => {
        commit('SET_CUSTOMERTREE', res.data)
      })
    }
  },
  // 
  getTimeLineInfo: ({ commit }, id) => {
    getTimeLineInfo(id).then(res => {
      commit('SET_ENDED_TYPE', res.data.ended_type) // 
      commit('SET_TIMELINEINFO', res.data.paths)
      commit('SHOW_SUGGESTION_SEND', res.data.show_suggestion_send)
    })
  },
  // 
  refreshTimeLineInfo: ({ dispatch }, id) => {
    // fd_ask
    if (id && id === state.lastAsk._id) {
      dispatch('getTimeLineInfo', id)
    } else if (!id) {
      Message.info('id', id)
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
