<template>
  <div class="tip-list">
    <template v-for="item of notificationList">
      <message-tips
        v-if="
          item.type === '-1' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'teacher'
        "
        :key="item._id"
        title=""
        footer-title=""
        btn-text=""
        @btnClose="btnFollowUpClose($event, item)"
        @btnCommit="btnFollowUpCommit(item)"
      >
        <div slot="content">
          <p class="info"></p>
        </div>
      </message-tips>
      <message-tips
        v-if="
          item.type === '-2' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'doctor'
        "
        :key="item._id"
        title=""
        footer-title=""
        btn-text=""
        @btnClose="btnFollowUpClose($event, item)"
        @btnCommit="btnFollowUpCommit(item)"
      >
        <div slot="content">
          <p class="info"></p>
        </div>
      </message-tips>
      <message-tips
        v-if="
          item.type === '-1' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'doctor'
        "
        :key="item._id"
        title=""
        footer-title=""
        btn-text=""
        @btnClose="btnFollowUpClose($event, item)"
        @btnCommit="btnFollowUpCommit(item)"
      >
        <div slot="content">
          <p class="info"></p>
        </div>
      </message-tips>
      <message-tips
        v-if="
          item.type === '-1' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'human_resource'
        "
        :key="item._id"
        title=""
        footer-title=""
        btn-text=""
        @btnClose="btnFollowUpClose($event, item)"
        @btnCommit="btnFollowUpCommit(item)"
      >
        <div slot="content">
          <p class="info"></p>
        </div>
      </message-tips>
      <message-tips
        v-if="
          item.type !== '-1' &&
            item.type !== '-2' &&
            item.status === 'normal' &&
            $store.state.user.mts_user.type === 'teacher'
        "
        :key="item._id"
        title=""
        @btnClose="btnFollowUpClose($event, item)"
      >
        <div slot="content">
          <p class="info">
            {{
              getStage(item.type)
            }}
          </p>
          <p class="info"></p>
        </div>
      </message-tips>
    </template>
  </div>
</template>

<script>
import MessageTips from '@/components/MessageTips'
import { minimize } from '@/api/learningAdmin'
export default {
  components: {
    MessageTips
  },
  props: {
    notificationList: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  computed: {
    getStage() {
      return function(stage) {
        if (stage === '0') return ''
        if (stage === '1') return ''
        if (stage === '2') return ''
        if (stage === '3') return ''
        if (stage === '4') return ''
        if (stage === '5') return ''
      }
    }
  },
  methods: {
    async btnFollowUpClose(e, item) {
      e.target.parentElement.parentElement.style.right = '-400px'
      const { data, code } = await minimize({
        id: item._id
      })
      if (code === 20000 && data.result) {
        this.$emit('obtainNotification')
      }
    },
    btnFollowUpCommit(item) {
      if (this.$route.name === 'studentsList') {
        window.location.href = '/learning-admin/studentsList?type=' + item.type
      } else this.$router.push('/learning-admin/studentsList?type=' + item.type)
    }
  }
}
</script>
