import request from '@/utils/request'

// 
export function getBasicInfo(babyId) {
  return request({
    url: `/family_member/${babyId}/basic_info`,
    method: 'get'
  })
}
// 
export function getCurrentCustomer(id) {
  return request({
    url: `/fd/health_ask/user/${id}/chat_list`,
    method: 'get'
  })
}

export function getBabyDetailById(id) {
  return request({
    url: `/family_member/${id}`,
    method: 'get'
  })
}
// 
export function addBabyRemark(id, params) {
  return request({
    url: `/family_member/${id}/add_remark`,
    method: 'put',
    data: params
  })
}

// 
export function chatUserRemark(id, params) {
  return request({
    url: `/user_manager/${id}`,
    method: 'put',
    data: params
  })
}
