import request from '@/utils/request'
// 
export function closeTask(taskId, data) {
  return request({
    url: `/userRecipe/${taskId}/close`,
    method: 'put',
    data
  })
}
// 
export function replayUser(taskId, data) {
  return request({
    url: `/userRecipe/replyUser/${taskId}`,
    method: 'post',
    data: data
    // mock: true
  })
}
// 
export function checkDesc(taskId, params = {}) {
  return request({
    url: `/userRecipe/checkDesc/${taskId}`,
    method: 'get',
    params: params
    // mock: true
  })
}
// /
export function upsetTask(id, type, data) {
  return request({
    url: `/userRecipe/upsetTask/${id}/type/${type}`,
    method: 'put',
    data
    // mock: true
  })
}
