import { getSingleAskMembers, getMembers } from '@/api/professionalRole'
import { systemRole } from '@/utils/dictionary'
const peopleByRole = {
  state: {
    allPeople: [], //
    obstetricsDdoctorOpts: [], //
    pediatricDoctorOpts: [], //
    endocrineDoc: [], //
    allDoctorPeople: [], //
    healthManagerOpts: [], //
    dieticiansOpts: [] //
  },
  mutations: {
    SET_PEOPLE: (state, payload) => {
      state.allPeople = payload.allPeople
      state.allDoctorPeople = payload.allDoctorPeople
      state.obstetricsDdoctorOpts = payload.obstetricsDdoctorOpts
      state.pediatricDoctorOpts = payload.pediatricDoctorOpts
      state.endocrineDoc = payload.endocrineDoc
      state.healthManagerOpts = payload.healthManagerOpts
      state.dieticiansOpts = payload.dieticiansOpts
    }
  },
  actions: {
    // 
    async filterPeopleByRole({ commit }, type) {
      const apiName = type === 'singleAsk' ? getSingleAskMembers : getMembers
      await apiName({ no_limit: 1 }).then(res => {
        const pediatricDoctorOpts = []
        const obstetricsDdoctorOpts = []
        const endocrineDoc = []
        const healthManagerOpts = []
        const dieticiansOpts = []
        let allDoctorPeople = []
        let allPeople = []
        const resData = res.data.list
        const key = type === 'singleAsk' ? 'single_ask_bus_role' : 'bus_role'
        resData.forEach(function(item) {
          if (item[key] === 'doctor' && type !== 'singleAsk') {
            // 
            if (item.department && item.department === 'pediatric') {
              pediatricDoctorOpts.push({
                label: item.name + '(' + systemRole(item[key], '') + ')',
                value: item._id
              })
            }
            // 
            if (item.department && item.department === 'obstetrics') {
              obstetricsDdoctorOpts.push({
                label: item.name + '(' + systemRole(item[key], '') + ')',
                value: item._id
              })
            }
            // 
            if (item.department && item.department === 'endocrine') {
              endocrineDoc.push({
                label: item.name + '(' + systemRole(item[key], '') + ')',
                value: item._id
              })
            }
          } else if (item[key] === 'doctor' && type === 'singleAsk') {
            allDoctorPeople.push({
              label: item.name + '(' + systemRole(item[key], '') + ')',
              value: item._id
            })
          }
          if (item[key] === 'hm') {
            healthManagerOpts.push({
              label: item.name + '(' + systemRole(item[key], '') + ')',
              value: item._id
            })
          }
          if (item[key] === 'dietician') {
            dieticiansOpts.push({
              label: item.name + '(' + systemRole(item[key], '') + ')',
              value: item._id
            })
          }
        })
        // 
        allDoctorPeople = allDoctorPeople.concat(
          obstetricsDdoctorOpts,
          pediatricDoctorOpts,
          endocrineDoc
        )
        // 
        allPeople = allPeople.concat(
          allDoctorPeople,
          healthManagerOpts,
          dieticiansOpts
        )
        let peopleObj = {
          allPeople, //
          allDoctorPeople, //
          healthManagerOpts, //
          dieticiansOpts, //
          obstetricsDdoctorOpts, //
          pediatricDoctorOpts, //
          endocrineDoc //
        }
        commit('SET_PEOPLE', peopleObj)
      })
    }
  }
}

export default peopleByRole
