// 
export function TransferStatusText(value, user, type, isAuto, role) {
  switch (value) {
    case 'apply':
      if (role === 'hm') {
        return '' + user.name + ''
      } else {
        return ''
      }
    case 'accept':
      if (role === 'hm') {
        if (isAuto) {
          return user.name + ''
        }
        return user.name + ''
      } else {
        return ''
      }
    case 'wait_user':
      return ''
    case 'start':
      if (isAuto) {
        return (
          user.name + '' + TransferType(type) + ''
        )
      }
      return user.name + TransferType(type) + ''
    case 'failed':
      return ''
    case 'asking':
      return user.name + TransferType(type) + ''
    case 'end':
    case 'reject':
      return ''
    default:
      return ''
  }
}
// 
export function TransferStatusTime(value, lastAsk, isAuto, role) {
  let startTime = ''
  let endTime = ''
  switch (value) {
    case 'apply':
      if (role === 'hm') {
        startTime = lastAsk.transfer_apply_at
        endTime = timeAddMinute(startTime, 5)
      }
      break
    case 'wait_user':
      startTime = lastAsk.transfer_wait_user_at
      endTime = timeAddMinute(startTime, 5)
      break
    case 'start':
      // / ,
      if (isAuto) {
        startTime = lastAsk.transfer_start_at
      }
      startTime = lastAsk.transfer_start_at
      break
    case 'accept':
    case 'reject':
    case 'fail':
    case 'end':
      break
    case 'asking':
      startTime = lastAsk.transfer_start_at
      break
    default:
      return ''
  }
  const time = {
    startTime: startTime,
    endTime: endTime
  }
  return time
}
// 
export function UserListTransferStatusIcon(session) {
  let dotColor = ''
  let iconType = ''
  let borderColor = ''
  let content = ''
  const value = session.last_ask.transfer_status
  const type = session.last_ask.transfer_type
  switch (value) {
    case 'start':
      if (session.status === 'asking' && session.last_ask.has_transfer) {
        if (type === 'doctor_voice') {
          // 
          dotColor = '#FFFFFF'
          iconType = 'icon-dhzhuanzhen'
          borderColor = '#ED3645'
          content = ''
        } else {
          // 
          dotColor = '#FFFFFF'
          iconType = 'icon-twzhuanzhen'
          borderColor = '#ED3645'
          content = ''
        }
      }
      break
    case 'asking':
      if (session.status === 'asking' && session.last_ask.has_transfer) {
        if (type === 'doctor_voice') {
          // 
          dotColor = '#FFFFFF'
          iconType = 'icon-dhzhuanzhen'
          borderColor = '#0085FF'
          content = ''
        } else {
          // 
          dotColor = '#FFFFFF'
          iconType = 'icon-twzhuanzhen'
          borderColor = '#0085FF'
          content = ''
        }
      }
      break
  }
  return {
    dotColor,
    iconType,
    borderColor,
    content
  }
}
// val:, minuteValminute
export function timeAddMinute(val, minuteVal) {
  // 2. 
  var date = new Date(val)
  var min = date.getMinutes()
  // 3. +5+5date
  date.setMinutes(min + minuteVal)
  // 4. 
  return date
}
