export function CoverStatus(status, replace) {
  const replaceName =
    replace && replace.be_replace && replace.be_replace.admin_user
      ? replace.be_replace.admin_user.name
      : ''
  const replaceNum =
    replace && replace.replace_list ? replace.replace_list.length : ''
  switch (status) {
    case 'none':
      return ''
    case 'replace_other':
      return `(${replaceNum})`
    case 'replace_by_other':
      return replaceName + '' // 
    default:
      return ''
  }
}
