/* eslint-disable prettier/prettier */
import { analysisNewMessage } from '@/utils/myMessage/message' // 
import _ from 'lodash'
import $store from '../../store'
const myIM = {
  state: {
    // 
    currentIMInfoMessages: [],
    current_fdAsk: '',
    trigger: '', // 
    noMessageFlag: true, //  true
    scrollBottomBtn: false // false
  },
  mutations: {
    //  
    SET_MESSAGE: (state, payload) => {
      state.currentIMInfoMessages = payload
    },
    SET_BOTTOM_BTN: (state, status) => {
      if (
        state.currentIMInfoMessages &&
        state.currentIMInfoMessages.length > 5 &&
        status
      ) {
        state.scrollBottomBtn = true
      } else {
        state.scrollBottomBtn = false
      }
    },
    SET_MESSAGE_FLAG: (state, flag) => {
      state.noMessageFlag = flag
    },
    SET_TRIGGER: (state, trigger) => {
      state.trigger = trigger
    },
    // 
    PUSH_MESSAGE: (state, payload) => {
      let existMesssage = ''
      if (state.currentIMInfoMessages) {
        existMesssage = _.find(state.currentIMInfoMessages, function(o) {
          return o._id === payload._id
        })
      }
      if (
        !existMesssage &&
        $store.state.chat.activedBabyId === payload.mymessage.family_member
      ) {
        state.currentIMInfoMessages.push(payload)
      }
    },
    PUT_MESSAGE_STATUS: (state, _id) => {
      for (let i = 0; i < state.currentIMInfoMessages.length; i++) {
        if (state.currentIMInfoMessages[i]._id === _id) {
          state.currentIMInfoMessages[i].msgStatus = true
        }
      }
    },
    UNSHIFT_MESSAGE: (state, msg) => {
      state.currentIMInfoMessages = msg.concat(state.currentIMInfoMessages)
    },
    ADD_MESSAGE: (state, msg) => {
      state.currentIMInfoMessages = state.currentIMInfoMessages.concat(msg)
    },
    REPLACE_MESSAGE(state, { oldMessage, oldMessageIndex, newMessage }) {
      state.currentIMInfoMessages.splice(oldMessageIndex, 1, newMessage)
      // watch
      // state.currentIMInfoMessages[oldMessageIndex] = newMessage
    },
    CURRENT_FD_ASK: (state, fdAsk) => {
      state.current_fdAsk = fdAsk
    }
    // 
  },
  actions: {
    // 
    setBottomBtn: ({ commit }, tar) => {
      commit('SET_BOTTOM_BTN', tar)
    },
    // 
    setTrigger: ({ commit }, tar) => {
      commit('SET_TRIGGER', tar)
    },
    setMessageFlag: ({ commit }, val) => {
      commit('SET_MESSAGE_FLAG', val)
    },
    //   chat.js
    setCurrentIMInfoMessages: ({ commit }, payload = []) => {
      const newMsgList = payload.map(item => {
        return analysisNewMessage(item)
      })
      commit('SET_MESSAGE', newMsgList)
    },
    unshiftMessages: ({ commit }, payload = []) => {
      const newMsgList = payload.map(item => {
        return analysisNewMessage(item)
      })
      commit('UNSHIFT_MESSAGE', newMsgList)
    },
    addMessages: ({ commit }, payload = []) => {
      const newMsgList = payload.map(item => {
        return analysisNewMessage(item)
      })
      commit('ADD_MESSAGE', newMsgList)
    },
    //  push  
    pushCurrentIMInfoMessages: ({ commit, dispatch }, newMessage) => {
      dispatch('setTrigger', 'mm', {
        root: true
      })
      commit('PUSH_MESSAGE', analysisNewMessage(newMessage))
    },
    // _id
    changeMessageReadSatus: ({ commit }, _id) => {
      commit('PUT_MESSAGE_STATUS', _id)
    },
    replaceMessage: ({ commit }, params) => {
      commit('REPLACE_MESSAGE', params)
    },
    goFdAsk: ({ state }, fdask) => {
      //      fdaskid $store.dispatch('goFdAsk', fdaskid)
      let existMesssage = {}
      const currentMsgList = state.currentIMInfoMessages
      if (currentMsgList) {
        existMesssage = _.find(currentMsgList, function(o) {
          return o.fdAsk === fdask
        })
      }
      if (existMesssage) {
        // 
        $store.dispatch('goAnchor', fdask)
      } else {
        $store.dispatch('chat/fdAskLocation', fdask, {
          root: true
        })
        setTimeout(() => {
          $store.dispatch('goAnchor', fdask)
        }, 700)
      }
    },
    async goMessageDetail({state,dispatch}, msgId){
      let existMesssage = ''
      const currentMsgList = state.currentIMInfoMessages
      if (currentMsgList && currentMsgList.length) {
        existMesssage = _.find(currentMsgList, function(o) {
          return o._id === msgId
        })
      }
      if (existMesssage) {
        // 
        dispatch('goMsgLocation',msgId)
      } else {
        await dispatch('chat/messageLocation', msgId, {
          root: true
        })
        dispatch('goMsgLocation',msgId)
      }

    },
    async goMsgLocation({dispatch},msgId){
      var parentbox = document.getElementById('chatBox')
      var anchor = document.getElementById(msgId) // id
      if (anchor && anchor.offsetTop) {
        parentbox.scrollTop = anchor.offsetTop - 150 // chrome
      } else {
        // 
        await dispatch('setCurrentIMInfoMessages', [], {
          root: true
        })
        await dispatch('chat/messageLocation', msgId, {
          root: true
        })
        let parentbox = document.getElementById('chatBox')
        let anchor = document.getElementById(msgId) // id
        if (anchor && anchor.offsetTop) {
          parentbox.scrollTop = anchor.offsetTop - 150 // chrome
        }
      }
    },
    goAnchor({ commit }, selector) {
      var parentbox = document.getElementById('chatBox')
      var anchor = document.getElementsByClassName(selector)[0] // id
      if (anchor && anchor.offsetTop) {
        parentbox.scrollTop = anchor.offsetTop - 100 // chrome
      }
    },
    scrollBottom({ commit, dispatch }) {
      const i = 150
      var container = document.getElementById('chatBox')
      // container.scrollTop
      //  container.scrollHeight - container.scrollTop === container.clientHeight
      container.scrollTop += i
      const scrollHeight = container.scrollHeight
      const clientHeight = container.clientHeight
      const scrollTop = container.scrollTop
      const cha = scrollHeight - scrollTop
      if (cha > clientHeight - 100) {
        var c = setTimeout(() => dispatch('scrollBottom'), 16)
      } else {
        clearTimeout(c)
      }
    },
    monitorFdAskChange({ commit }, fdAsk) {
      commit('CURRENT_FD_ASK', fdAsk)
    }
  }
}
export default myIM
