/* eslint-disable prettier/prettier */
const _ = require('lodash')

const getters = {
  // 
  templateDic: state => state.recipes.templateDic,
  recipesTaskList: state => state.recipes.recipesTaskList,
  recipesTaskCount: state => state.recipes.taskCount,
  creaters: state => state.recipes.creater,
  senders: state => state.recipes.sender,
  // 
  // ()
  babyList: state => state.health.babyList,
  babyExtendTab: state => state.babyExtend.activeInfo,
  selectBaby: state => state.health.selectBaby,
  sendStatus: state => state.health.sendStatus,
  taskCount: state => state.health.taskCount,
  serviceDic: state => state.health.serviceDic,
  // TODO: id
  babyDetailById: state => state.babyExtend.babyDetailById,
  // 
  sidebar: state => state.app.sidebar,
  device: state => state.app.device,
  token: state => state.user.token,
  avatar: state => state.user.avatar,
  name: state => state.user.name,
  roles: state => state.user.roles,
  userInfo: state => state.user.userInfo,
  getReplaceStatus: state => state.user.myReplaceStatus,
  bus_role: state => state.user.bus_role,
  role: state => state.user.transferRole,
  habit: state => state.user.habit,
  permission_routes: state => state.permission.routes,
  newMessage: state => state.im.newMessage, // 
  loginInfo: state => state.im.loginInfo,
  // 
  allPeople: state => state.peopleByRole.allPeople,
  allDoctorPeople: state => state.peopleByRole.allDoctorPeople,
  pediatricDoctorOpts: state => state.peopleByRole.pediatricDoctorOpts,
  obstetricsDdoctorOpts: state => state.peopleByRole.obstetricsDdoctorOpts,
  endocrineDoc: state => state.peopleByRole.endocrineDoc,
  healthManagerOpts: state => state.peopleByRole.healthManagerOpts,
  dieticiansOpts: state => state.peopleByRole.dieticiansOpts,
  // 
  getCurrentIMInfoMessages: state => state.myIM.currentIMInfoMessages,
  currentIMInfo: state => state.im.loginInfo,
  imUserInfo: state => state.im.loginInfo,
  trigger: state => state.myIM.trigger,
  noMessageFlag: state => state.myIM.noMessageFlag,
  // 
  cusSearchValue: state => state.chat.cusSearchValue,
  // 
  customerTree: state => {
    let tree = state.chat.customerTree
    const searchValue = state.chat.cusSearchValue
    // console.log('searchValue', searchValue)
    // 
    tree = tree.map(group => {
      group.hms.map(nurse => {
        nurse.sessions = nurse.sessions.filter(
          session =>
            session.user.name.includes(searchValue) ||
            session.user.remark_name.includes(searchValue) ||
            session.user.user_id.includes(searchValue)
        )
        return nurse
      })
      return group
    })
    return tree
  },
  isMyUser: state => state.chat.isMyUser,
  activedBabyIndex: state => state.chat.activedBabyIndex,
  scrollBottomBtn: state => state.myIM.scrollBottomBtn, // 
  // 
  timeLineInfo: state => state.chat.timeLineInfo,
  sessionId: state => state.msgQueue.sessionId,
  current_fdAsk: state => state.myIM.current_fdAsk, // id
  currentSession: state => state.chat.currentSession,
  sessionLastAsk: state => state.chat.sessionLastAsk,
  serviceItem: state => state.chat.serviceItem,
  lastAsk: state => state.chat.lastAsk,
  unReadCount: state => state.chat.unReadCount,

  // 
  currentCustomer: state => state.chat.currentCustomer,
  activedUserInfo: state => state.chat.activedUserInfo,
  activedBabyId: state => state.chat.activedBabyId, //id
  activedUserId: state => state.chat.activedUserId, //id
  activedBaby: state => state.chat.activedBaby, // 
  // 
  currentBabyList: state => state.chat.currentBabyList,
  // 
  askingCustomerList: state => state.msgQueue.askingCustomerList,
  // 
  transferCustomerList: state => state.msgQueue.transferCustomerList,
  // 
  waitingCustomerList: state => state.msgQueue.waitingCustomerList,
  // 
  endedType: state => state.chat.ended_type,
  showSend: state => state.chat.show_suggestion_send,
  // textAnswer
  currentTextAnswer: state => {
    let currentTextAnswer = state.chat.currentTextAnswer
    const mytimeLineInfo = state.chat.timeLineInfo
    currentTextAnswer = _.find(mytimeLineInfo, function(o) {
      return o.type === 'text_answer'
    })
    return currentTextAnswer
  },
  currentTransferTextAnswer: state => {
    let currentTransferTextAnswer = state.chat.currentTransferTextAnswer
    const mytimeLineInfo = state.chat.timeLineInfo
    currentTransferTextAnswer = _.find(mytimeLineInfo, function(o) {
      return o.type === 'text_transfer_answer'
    })
    return currentTransferTextAnswer
  },
  // voice_transfer_answer
  currentVoiceAnswer: state => {
    let currentVoiceAnswer = state.chat.currentVoiceAnswer
    const mytimeLineInfo = state.chat.timeLineInfo
    currentVoiceAnswer = _.find(mytimeLineInfo, function(o) {
      return o.type === 'voice_transfer_answer'
    })
    return currentVoiceAnswer
  },
  manageAnswer: state => {
    // 
    let manageAnswer = state.chat.manageAnswer
    const mytimeLineInfo = state.chat.timeLineInfo
    manageAnswer = _.find(mytimeLineInfo, function(o) {
      return (
        o.type === 'text_answer' ||
        o.type === 'text_transfer_answer' ||
        o.type === 'voice_transfer_answer'
      )
    })
    return manageAnswer
  },
  notify: state => state.diagnose.notify, // 
  // 
  // TODO: ,// 
  diagnose_statue: state => state.diagnose.diagnose_statue,
  replyBoxFlag: state => state.diagnose.replyBoxFlag,
  newAskMessage: state => state.ask.newMessage,
  newSingleAsk: state => state.ask.newSingleAsk,
  singleAskSelect: state => state.ask.singleAskSelect,
  oneChannelOptions: state => state.crm.oneChannel,
  twoChannelOptions: state => state.crm.twoChannel,
  threeChannelOptions: state => state.crm.threeChannel,
  sourceChannelOptions: state => state.crm.sourceChannelOptions,
  // 
  askMode: state => state.chat.ask_mode,
  // messageQueue
  chatListType: state => state.chat.chatListType,
  msgQueue: state => state.msgQueue.msgQueue
}

export default getters
